.portfolio_section {
    background: #EDF3FA;
    text-align: center;
    padding: 120px 20px;
    font-family: 'NanumSquareRound';

    .portfolio_section_title {
        font-size:32px;
        color: #2E3A4E;
    }

    .portfolio_section_subtitle {
        font-size: 18px;
        margin-bottom: 40px;
        color: #2E3A4E;
    }

    .portfolio_grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        max-width: 1000px;
        margin: 0 auto;
        gap: 100px;
        
        .project_card {
            padding: 20px;
            border-radius: 16px;
            color: #2E3A4E;
            transition: transform 0.3s ease, box-shadow 0.3s ease;
            cursor: pointer;
            text-align: left;
            background-color: #fff;
            justify-items: center;
            width: 400px;
    
            &:hover {
                transform: scale(1.05);
                box-shadow: 0 10px 20px #6182AB;
            }
    
            h3 {
                font-size: 20px;
                margin-bottom: 10px;
            }
    
            p {
                font-size: 14px;
                margin-bottom: 12px;
            }
    
            .skill_icons {
                display: flex;
                flex-wrap: wrap;
                gap: 8px;
                margin-top: 8px;
                justify-content: center;
    
                .skill_icon {
                    width: 28px;
                    height: 28px;
                    object-fit: contain;
                    border-radius: 6px;
                    background: #fff;
                    padding: 4px;
                }
            }
    
            .projectThumb {
                width: 100%;
                height: 200px;
                object-fit: cover;
            }
        }
    }

}

.modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;

    .modal_content {
        background: #2E3A4E;
        padding: 30px;
        border-radius: 12px;
        max-width: 700px;
        max-height: 80vh;
        width: 90%;
        text-align: left;
        color: #fff;
        overflow-y: auto; // 세로 스크롤 가능

        h3 {
            margin-top: 20px;
        }

        button {
            margin-top: 20px;
            padding: 8px;
            border: none;
            border-radius: 8px;
            background: #EDF3FA;
            color: black;
            cursor: pointer;

            &:hover {
                background: #d2e5f8;
            }
        }

        .modal_skill_icons {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
            margin-top: 8px;

            .modal_skill_icon {
                width: 28px;
                height: 28px;
                object-fit: contain;
                border-radius: 6px;
                background: #fff;
                padding: 4px;
            }
        }

        .modalMeta {
            margin-bottom: 70px;
        }

        .linkButtons {
            margin-top: 0.5rem;
            display: flex;
            gap: 0.5rem;
            flex-wrap: wrap;

            .linkButton {
                background-color: #EDF3FA;
                color: #333;
                padding: 0.4rem 0.75rem;
                border-radius: 0.5rem;
                font-size: 0.875rem;
                text-decoration: none;

                &:hover {
                    background-color: #d2e5f8;
                }
            }
        }

        .section_divider {
            border: none;
            border-top: 2px solid #ccc;
            margin: 40px 0 20px;
            width: 100%;
        }

        .modal_exImages {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            justify-items: center;
            gap: 20px;
            margin-top: 24px;
            margin-bottom: 40px;

            .modal_exImage {
                width: 100%;
                height: 240px;
                margin-bottom: 40px;
            }
        }

        .dimages {
            margin-bottom: 1rem;
            margin-top: 3rem;
            width: 100%;
        }

        .modal_details {
            transform: translateX(-20px);

            .dtitles {
                margin-top: 3rem;
            }

            .modal_descs {
                margin-top: 1rem;
            }
        }
    }
}